@mixin medium-extra-thick {
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
}

@mixin small-extra-thick {
    font-size: 13px;
    font-weight: 600;
    line-height: 13px;
}

@mixin super-large-thick {
    font-weight: 500;
    font-size: 40px;
    line-height: 40px;
}

@mixin huge-thick {
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
}

@mixin medium-thick {
    font-size: 13px;
    font-weight: 500;
    line-height: 13px;
}

@mixin thick {
    font-size: 12px;
    font-weight: 500;
    line-height: 12px;
}

@mixin tall-thick {
    font-size: 12px;
    font-weight: 500;
    line-height: 15px;
}

@mixin small-tall-thick {
    font-size: 11px;
    font-weight: 500;
    line-height: 12px;
}

@mixin small-thick {
    font-size: 11px;
    font-weight: 500;
    line-height: 11px;
}

@mixin tiny-thick {
    font-weight: 500;
    font-size: 9px;
    line-height: 9px;
}

@mixin small {
    font-size: 8px;
    line-height: 8px;
}
